// BASIC
import React, { Component } from 'react'
import styled, {css} from 'styled-components'
import {au, db} from '../../../Config/firebase'
import anime from 'animejs/lib/anime.es.js';
// IMAGES
import bottle from '../../Images/icons/bottle.svg'

const BottleSpinComponent = styled.div`
	width: 100vw;
	height: 100vh;
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	align-items: center;
	overflow: hidden;
`
const BottleWrapper = styled.div`
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
`
const Bottle = styled.img`
	width: 130px;
	@media (min-width: 375px) {
		width: 150px;
	}
`
const Counter = styled.div`
	font-weight: bold;
	font-size: 30px;
	color: var(--color-main);
`
const Div = styled.div`
	width: 100px;
	height: 100px;
	transform: rotate(-90deg);
`
const Avatar = styled.div`
	position: absolute;
	width: 50px;
	height: 50px;
	background: url("${props => props.src}") no-repeat center center;
	background-size: cover;
	border-radius: 100%;
	opacity: 0.7;
	transition: all 0.3s ease;
	transform: rotate(90deg);
`

class BottleSpin extends Component {
	state = {
		randomNum: 1 + Math.random(),
		counter: 0,
		complete: true,
		users: []
	}
	componentDidMount() {
		au.onAuthStateChanged(user => {
			if (user) {
				db.collection('bottle-spin').doc(this.props.match.params.teamName).get().then(snap => {
					snap.data().users.forEach(uid => {
						db.collection('users').doc(uid).get().then(snap => {
							this.setState(prevState => ({
								users: [
									...prevState.users,
									snap.data().info.avatar
								]
							}));
							this.setRing()
						})
					})
				})
			}
		})
		// const parentDiv = document.getElementById("parentDiv")
		// const children = [...parentDiv.children]
		// if (children.length) {
		// 	const childLength = children.length
		// 	const div = 360 / childLength
		// 	const radius = window.innerWidth > 375 &&  window.innerHeight >= 375 ? 150 : 125
		// 	const offsetToParentCenter = parseInt(parentDiv.offsetWidth / 2)
		// 	const offsetToChildCenter = children[0].offsetWidth / 2
		// 	const totalOffset = offsetToParentCenter - offsetToChildCenter
		// 	children.forEach((child, i) => {
		// 		const y = Math.sin(div * (i + 1) * (Math.PI / 180)) * radius
		// 		const x = Math.cos(div * (i + 1) * (Math.PI / 180)) * radius
		// 		child.style.top = `${y + totalOffset}px`
		// 		child.style.left = `${x + totalOffset}px`
		// 	})
		// }
		
	}
	setRing = () => {
		const parentDiv = document.getElementById("parentDiv")
		const children = [...parentDiv.children]
		if (children.length) {
			const childLength = children.length
			const div = 360 / childLength
			const radius = window.innerWidth > 375 &&  window.innerHeight >= 375 ? 150 : 125
			const offsetToParentCenter = parseInt(parentDiv.offsetWidth / 2)
			const offsetToChildCenter = children[0].offsetWidth / 2
			const totalOffset = offsetToParentCenter - offsetToChildCenter
			children.forEach((child, i) => {
				const y = Math.sin(div * (i + 1) * (Math.PI / 180)) * radius
				const x = Math.cos(div * (i + 1) * (Math.PI / 180)) * radius
				child.style.top = `${y + totalOffset}px`
				child.style.left = `${x + totalOffset}px`
			})
		}
		const randomValues = () => {
			anime({
				targets: '#bottle',
				rotate: () => `${this.state.randomNum}turn`,
				duration: 5000,
				complete: () => {
					this.setState({
						complete: true
					})
				}
			});
		}
		document.getElementById("bottle").addEventListener('click', () => {
			if (this.state.complete) {
				this.setState({complete: false})
				randomValues()
				console.log();
				[...parentDiv.children].forEach(child => {
					child.style.opacity = '0.7'
					child.style.transform = 'rotate(90deg) scale(1)'
				})
				const vertices = this.state.users.length
				const corner = 360 / vertices
				const degress = (this.state.randomNum - Math.floor(this.state.randomNum)) * 360
				const choosedElement = Math.round(degress / corner) % vertices === 0 ? vertices - 1 : (Math.round(degress / corner) % vertices) - 1
				console.log(degress, choosedElement)
				setTimeout(() => {
					document.getElementById("parentDiv").children[choosedElement].style.opacity = '1'
					document.getElementById("parentDiv").children[choosedElement].style.transform = 'rotate(90deg) scale(1.5)'
					this.setState(prevState => ({
						randomNum: prevState.randomNum + 1 + Math.random(),
						counter: prevState.complete ? prevState.counter + 1 : prevState.counter,
					}));
				}, 5000)
			}
		})
	}
	render() {
		return (
			<BottleSpinComponent>
				{/* <Counter>{this.state.counter}</Counter> */}
				<Div id="parentDiv">
					{this.state.users.map(avatar => <Avatar src={avatar} />)}
				</Div>
				<BottleWrapper>
					<Bottle id="bottle" src={bottle} />
				</BottleWrapper>
			</BottleSpinComponent>
		);
	}
}

export default BottleSpin