// BASIC
import React, { Component } from 'react'
import styled, {css, keyframes} from 'styled-components'
// ICONS
import {FaPlayCircle} from 'react-icons/fa'
// IMAGES
import loader from '../../../Images/loader.svg'

const fade = keyframes`
	from {
		opacity: 0;
		transform: translate(50%, 50%) scale(0.9);
	}
	to {
		opacity: 1;
		transform: translate(50%, 50%) scale(1);
	}
`
const goMobile = keyframes`
	from {
		bottom: 50%;
		right: 50%;
		transform: translate(50%, 50%);
	}
	to {
		bottom: -40px;
		right: -40px;
		transform: translate(0) scale(0.333);
	}
`
const go = keyframes`
	from {
		bottom: 50%;
		right: 50%;
		transform: translate(50%, 50%);
	}
	to {
		bottom: -20px;
		right: -20px;
		transform: translate(0) scale(0.333);
	}
`
const showAndGoMobile = keyframes`
	from {
		bottom: 50%;
		right: 50%;
		transform: translate(50%, 50%) scale(0.9);
		opacity: 0;
	}
	30% {
		bottom: 50%;
		right: 50%;
		transform: translate(50%, 50%) scale(0.9);
	}
	to {
		bottom: -40px;
		right: -40px;
		transform: translate(0) scale(0.333);
	}
`
const showAndGo = keyframes`
	from {
		bottom: 50%;
		right: 50%;
		transform: translate(50%, 50%);
	}
	to {
		bottom: -20px;
		right: -20px;
		transform: translate(0) scale(0.333);
	}
`
const Choose = styled.div`
	position: absolute;
	bottom: 50%;
	right: 50%;
	transform: translate(50%, 50%);
	width: 150px;
	height: 150px;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: var(--color-main);
	font-size: 70px;
	font-weight: bold;
	color: var(--color-bg);
	text-transform: uppercase;
	border-radius: 100%;
	overflow: hidden;
	/* transition: all  ease; */
	animation: ${fade} 0.3s ease;
	:hover {
		cursor: pointer;
	}
	${props =>
		props.go &&
		css`
			animation: ${goMobile} 0.5s 0.5s both;
			@media (min-width: 800px) {
				animation: ${go} 0.5s 0.5s both;
			}
		`
	}
	${props =>
		props.showAndGo &&
		css`
			animation: ${showAndGoMobile} 0.5s 0.5s both;
			@media (min-width: 800px) {
				animation: ${showAndGo} 0.5s 0.5s both;
			}
		`
	}

`
const waitAnimation = keyframes`
	from {
		transform: translate(50%, 50%) scale(1) rotate(0);
	}
	16.66% {
		transform: translate(50%, 50%) scale(0.8) rotate(60deg);
	}
	33.33% {
		transform: translate(50%, 50%) scale(1) rotate(120deg);
	}
	50% {
		transform: translate(50%, 50%) scale(0.8) rotate(180deg);
	}
	66.66% {
		transform: translate(50%, 50%) scale(1) rotate(240deg);
	}
	83.33% {
		transform: translate(50%, 50%) scale(0.8) rotate(300deg);
	}
	100% {
		transform: translate(50%, 50%) scale(1) rotate(360deg);
	}
`
const Wait = styled.img`
	position: absolute;
	bottom: 50%;
	right: 50%;
	transform: translate(50%, 50%);
	width: 150px;
	height: 150px;
	animation: ${waitAnimation} 3s linear infinite;
`
const Play = styled(FaPlayCircle)`
	font-size: 140px;
`

class ChooseLetter extends Component {
	state = {
		index: 'nope',
		running: false,
		alphabet: ['a', 'b', 'c', 'd', 'e', 'f', 'g', 'h', 'i', 'j', 'k', 'l', 'ł', 'm', 'n', 'o', 'p', 'r', 's', 't', 'u', 'w', 'z', 'ź', 'ż'],
	}
	run = () => {
		this.setState(prevState => ({
			index: prevState.index + 1 <= this.state.alphabet.length ? prevState.index + 1 : 0
		}));
	}
	start = () => {
		this.props.setChoosing(true);
		this.choose = setInterval(this.run, 50);
		this.setState({running: true})
	}
	stop = () => {
		if (this.props.owner) {
			clearInterval(this.choose);
			this.props.setChoosing(false);
			setTimeout(() => this.props.back(this.state.alphabet[this.state.index]), 1000);
		}
	}
	render() {
		return (
			<>
				{this.props.owner ? <Choose go={this.props.go} title="Losuj!">
					{this.state.index === 'nope' ? <Play onClick={this.start} /> : <span onClick={this.stop}>{this.state.alphabet[this.state.index]}</span>}
				{/* </Choose> : <Choose go={this.props.go} title="Trwa losowanie literki...">...</Choose>} */}
				</Choose> : (!this.props.go ? <Wait src={loader} /> : <Choose showAndGo>?</Choose>)}
			</>
		);
	}
}

export default ChooseLetter