// BASIC
import React, { Component } from 'react'
import styled, { css, keyframes } from 'styled-components'
// IMAGES
import lightning from '../Images/logo/lightning.svg'

const hide = keyframes`
	0% {
		opacity: 1;
	}

	100% {
		opacity: 0;
	}
`
const PreloaderElement = styled.div`
	display: flex;
	justify-content: center;
	align-items: center;
	position: absolute;
	top: 0;
	width: 100vw;
	height: 100vh;
	overflow: hidden;
	background-color: black;
	z-index: 100;
	${props =>
		props.hide &&
		css`
			animation: ${hide} 0.3s both;
		`
	}
	${props =>
		props.delete &&
		css`
			display: none;
		`
	}
`

const Animation = styled.div`
	display: none;
	position: relative;
	width: 500px;
	height: 500px;
	${props =>
		props.isLoaded &&
		css`
			display: flex;
		`
	}
`
const lightningAnimation = keyframes`
	0% {
		opacity: 0;
		transform: translate(-50%, -50%) scale(0.5);
	}
	30% {
		opacity: 1;
		transform: translate(-50%, -50%) scale(1.1);
	}
	70% {
		opacity: 1;
		transform: translate(-50%, -50%) scale(1);
	}
	100% {
		opacity: 1;
		transform: translate(-50%, -50%) scale(1);
	}
`
const Logo = styled.img`
	width: 100vw;
	max-width: 512px;
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	:first-child{
		animation: ${lightningAnimation} 1s both;
	}
`

class Preloader extends Component {
	state = {
		isLoaded: false,
		isLoading: true,
		hide: false,
		delete: false
	}
	componentDidMount() {
		this.setState({isLoaded: true, isLoading: false})
		setTimeout(() => {this.setState({hide: true})}, 1000);
		setTimeout(() => {this.setState({delete: true})}, 1300);
	}
	componentWillUnmount() {
		this.setState({
			isLoaded: false,
			isLoading: true,
			hide: false,
			delete: false
		})
	}
	render() {
		return (
			<PreloaderElement hide={this.state.hide} delete={this.state.delete}>
				<Animation isLoaded={this.state.isLoaded}>
					<Logo src={lightning} />
					{/* <HideLine /> */}
				</Animation>
			</PreloaderElement>
		);
	}
}

export default Preloader