// BASIC
import React, { Component } from 'react'
import styled from 'styled-components'
import {au, db} from '../../Config/firebase'
const OpinionsComponent = styled.div`
	
`

class Opinions extends Component {
	state = {
		userId: null,
		opinions: null
	}
	componentDidMount() {
		au.onAuthStateChanged(user => {
			if (user) {
				this.setState({userId: user.uid})
			}
			else {
				this.setState({userId: 'nope'})
			}
		})
		db.collection('apps').doc('epic-brain').get().then(snap => {
			this.setState({opinions: snap.data().stats.opinions});
		})
	}
	render() {
		return (
			<OpinionsComponent>
				{!this.state.opinions ? 'Wczytywanie...' : <>
					{this.state.opinions.map(opinion => {
						return (
							<div>{opinion.stars}</div>
						)
					})}
				</>}
				{this.state.userId && <>
					{this.state.userId === 'nope' ? 'Zaloguj się!' : 'Ok'}
				</>}
			</OpinionsComponent>
		);
	}
}

export default Opinions