// BASIC
import React, { Component } from 'react'
import styled, {keyframes} from 'styled-components'
import { db, au } from '../../../../Config/firebase'
// COMPONENTS
import ChooseLetter from '../components/ChooseLetter'
import Info from '../../components/Info'
// ICONS
import {FaCheck} from 'react-icons/fa'

const GameSite = styled.div`
	width: 100vw;
	height: 100vh;
	height: calc(var(--vh, 1vh) * 100);
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 30px 70px;
	text-align: center;
	@media (min-width: 800px) {
		padding: 30px 110px;
	}
`
const TeamName = styled.h1`
	margin: 0 0 20px 0;
	font-size: 30px;
`
const fade = keyframes`
	from {
		opacity: 0;
		transform: scale(0.9);
	}
	to {
		opacity: 1;
		transform: scale(1);
	}
`
const All = styled.main`
	max-height: calc(100vh - 175px);
	overflow-y: scroll;
	scrollbar-width: none;
	::-webkit-scrollbar {
		width: 0;
	}
`
const Cathegories = styled.div`
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	animation: ${fade} 0.2s ease;
`
const Cathegory = styled.div`
	text-align: center;
	width: 100%;
	margin: 0 10px 0 20px;
	@media (min-width: 550px) {
		width: 150px;
	}
`
const Title = styled.h3`
	font-size: 20px;
	margin: 15px;
`
const Input = styled.input`
	background-color: transparent;
	border: 1px solid var(--color-main);
	padding: 10px;
	outline: none;
	color: var(--color-primary);
	text-align: center;
	border-radius: 10px;
	width: 100%;
	font-size: 18px;
	::placeholder {
		transition: all 0.2s ease;
		color: var(--color-secondary);
	}
	:focus {
		::placeholder {
			transform: scale(0.5);
			opacity: 0;
		}
	}
`
const Check = styled(FaCheck)`
	font-size: 35px;
	margin-top: 30px;
	color: var(--color-main);
	animation: ${fade} 0.2s ease;
`
const Timer = styled.div`
	position: absolute;
	bottom: 10px;
	left: 10px;
	width: 50px;
	height: 50px;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: var(--color-main);
	font-size: 25px;
	font-weight: bold;
	color: var(--color-bg);
	border-radius: 100%;
	animation: ${fade} 0.2s ease;
	@media (min-width: 800px) {
		bottom: 30px;
		left: 30px;
	}
`
const Letter = styled.div`
	width: 50px;
	height: 50px;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: var(--color-main);
	margin-top: 20px;
	font-size: 25px;
	font-weight: bold;
	text-transform: uppercase;
	color: var(--color-bg);
	border-radius: 100%;
	animation: ${fade} 0.2s ease;
`
const Users = styled.div`
	position: absolute;
	bottom: 10px;
	right: 10px;
	@media (min-width: 800px) {
		bottom: 30px;
		right: 30px;
	}
`
const Avatar = styled.div`
	width: 50px;
	height: 50px;
	background: url("${props => props.src}") no-repeat center center;
	background-size: cover;
	margin: 20px 0;
	border-radius: 100%;
	animation: ${fade} 0.2s ease;
`
class Game extends Component {
	state = {
		owner: null,
		userId: null,
		round: 1,
		rounds: null,
		userRound: {
			country: '-',
			city: '-',
			plant: '-',
			animal: '-',
			item: '-',
			name: '-'
		},
		seconds: null,
		letter: false,
		choosing: true,
		timer: false,
		userImages: []
	}
	componentDidMount() {
		au.onAuthStateChanged(user => {
			if (user) {
				// db.collection('apps-data').doc(this.props.match.params.teamName).update({
				// 	[`users.${user.uid}`]: {
				// 		rounds: [],
				// 		userId: user.uid,
				// 		points: 0,
				// 	}
				// })
				this.setState({userId: user.uid})
				db.collection('apps-data').doc(this.props.match.params.teamName).get().then(snap => {
					this.setState({owner: snap.data().owner})
					const userImages = []
					Object.keys(snap.data().users).forEach((uid) => {
						db.collection('users').doc(uid).get().then(snap => {
							userImages.push(snap.data().info.avatar)
							this.setState({userImages})
						})
					})
				})
				db.collection('apps-data').doc(this.props.match.params.teamName).onSnapshot(snap => {
					console.log(!Object.keys(snap.data().users).includes(user.uid))
					const data = snap.data()
						if (!Object.keys(snap.data().users).includes(user.uid)) {
							db.collection('apps-data').doc(this.props.match.params.teamName).update({
									[`users.${user.uid}`]: {
										userId: user.uid,
										points: 0,
										rounds: [],
										addData: false
									}
							})
						}
						else if (data.users[user.uid]) {
							if (data.users[user.uid].addData) {
								this.ready()
								db.collection('apps-data').doc(this.props.match.params.teamName).update({
									[`users.${user.uid}.addData`]: false
								})
							}
							this.setState({
								round: data.round,
								rounds: data.users[user.uid].rounds,
								letter: data.letter,
								seconds: data.seconds,
								choosing: snap.data().choosing,
								timer: data.timer
							})
						}
				})
			}
		})
	}
	updateData = (e) => {
		e.persist();
		if (!e.target.value) {
			this.setState(prevState => ({
				userRound: {
					...prevState.userRound,
					[e.target.id]: '-'
				}
			}));
		}
		else if (e.target.value.charAt(0).toLowerCase() === this.state.letter) {
			this.setState(prevState => ({
				userRound: {
					...prevState.userRound,
					[e.target.id]: e.target.value
				}
			}));
		}
		else {
			e.target.value = ''
		}
	}
	clear = () => {
		[...document.getElementsByClassName('input')].forEach(element => {
			element.value = '';
		})
	}
	endGame = () => {
		db.collection('apps-data').doc(this.props.match.params.teamName).update({
			timer: true
		})
		this.timer = setInterval(() => {
			db.collection('apps-data').doc(this.props.match.params.teamName).get().then(snap => {
				if (snap.data().seconds) {
					db.collection('apps-data').doc(this.props.match.params.teamName).update({
						seconds: snap.data().seconds - 1
					})
				}
				else {
					db.collection('apps-data').doc(this.props.match.params.teamName).update({
						seconds: 30
					})
				}
				if (this.state.seconds === 0) {
					clearInterval(this.timer);
					setTimeout(() => {
						// alert('Koniec gry!')
						db.collection('apps-data').doc(this.props.match.params.teamName).get().then(snap => {
							Object.keys(snap.data().users).forEach(uid => {
								db.collection('apps-data').doc(this.props.match.params.teamName).update({
									[`users.${uid}`]: {
										points: 0,
										rounds: [],
										appData: false,
									}
								})
							})
							db.collection('apps-data').doc(this.props.match.params.teamName).update({
								letter: '',
								seconds: '-',
								timer: false,
								round: 0,
								choosing: true
							})
						})
					}, 3000);
				}
			})
		}, 300);
	}
	setTimer = () => {
		// db.collection('apps-data').doc(this.props.match.params.teamName).update({
		// 	timer: true
		// })
		this.timer = setInterval(() => {
			db.collection('apps-data').doc(this.props.match.params.teamName).get().then(snap => {
				if (snap.data().seconds === '-' && snap.data().timer === false) {
					db.collection('apps-data').doc(this.props.match.params.teamName).update({
						seconds: 30,
						timer: true
					})
				}
				else {
					if (snap.data().seconds > 0) {
						db.collection('apps-data').doc(this.props.match.params.teamName).update({
							seconds: snap.data().seconds - 1
						})
					}
					else {
						clearInterval(this.timer);
						setTimeout(() => {
							db.collection('apps-data').doc(this.props.match.params.teamName).update({
								seconds: '-',
								timer: false,
								round: snap.data().round + 1,
								letter: '',
								choosing: true
							}).then(() => {
								db.collection('apps-data').doc(this.props.match.params.teamName).get().then(snap => {
									Object.values(snap.data().users).forEach((user, index) => {
										console.log(user.rounds.length, snap.data().round)
										if (user.rounds.length < snap.data().round) {
											console.log('ten: ', user);
											const uid = Object.keys(snap.data().users)[index];
											db.collection('apps-data').doc(this.props.match.params.teamName).update({
												[`users.${uid}.addData`]: true
											})
										}
									})
								})
							})
						}, 1000);
					}
				}
			})
		}, 300);
	}
	ready = () => {
		db.collection('apps-data').doc(this.props.match.params.teamName).get().then(snap => {
			db.collection('apps-data').doc(this.props.match.params.teamName).update({
				[`users.${this.state.userId}.rounds`]: [
					this.state.userRound,
					...snap.data().users[this.state.userId].rounds,
				]
			}).then(() => {
				console.log(snap.data().users[this.state.userId].addData);
				if (!snap.data().users[this.state.userId].addData) {
					if (this.state.rounds.length >= 3) {
						this.endGame()
						console.log('endgame');
					}
					else {
						this.setTimer()
						console.log('settimer');
					}
				}
			})
		})
		this.clear()
	}
	finishChoosing = (letter) => {
		db.collection('apps-data').doc(this.props.match.params.teamName).update({
			letter: letter
		})
	}
	setChoosing = (bool) => {
		db.collection('apps-data').doc(this.props.match.params.teamName).get().then(snap => {
			Object.keys(snap.data().users).forEach(uid => {
				db.collection('apps-data').doc(this.props.match.params.teamName).update({
					choosing: bool
				})
			})
		})
	}
	logIn = () => {
		this.props.history.push('/logowanie')
	}
	render() {
		return (
			<>
				{this.state.userId ? <GameSite>
					{!this.state.owner ? '...' : <>
						<TeamName>{this.props.match.params.teamName}</TeamName>
							{!this.state.letter ? <>
								{this.state.owner !== this.state.userId ? <ChooseLetter go={!this.state.choosing} /> : <ChooseLetter owner go={!this.state.choosing} back={this.finishChoosing} setChoosing={this.setChoosing} />}
							</> : <All>
								{this.state.rounds.reverse().map(({country, city, plant, animal, item, name}, index) => <Cathegories key={`cathegory-${index}`}>
										<Cathegory>
											<Title>Państwo</Title>
											<Input value={country} readOnly />
										</Cathegory>
										<Cathegory>
											<Title>Miasto</Title>
											<Input value={city} readOnly />
										</Cathegory>
										<Cathegory>
											<Title>Roślina</Title>
											<Input value={plant} readOnly />
										</Cathegory>
										<Cathegory>
											<Title>Zwierzę</Title>
											<Input value={animal} readOnly />
										</Cathegory>
										<Cathegory>
											<Title>Rzecz</Title>
											<Input value={item} readOnly />
										</Cathegory>
										<Cathegory>
											<Title>Imię</Title>
											<Input value={name} readOnly />
										</Cathegory>
								</Cathegories>)}
								{this.state.rounds.length >= 3 ? '' : <>
									<Cathegories>
										<Cathegory>
											<Title>Państwo</Title>
											<Input id="country" className="input" onChange={this.updateData} placeholder="Wpisz Państwo" autoComplete="off" />
										</Cathegory>
										<Cathegory>
											<Title>Miasto</Title>
											<Input id="city" className="input" onChange={this.updateData} placeholder="Wpisz Miasto" autoComplete="off" />
										</Cathegory>
										<Cathegory>
											<Title>Roślina</Title>
											<Input id="plant" className="input" onChange={this.updateData} placeholder="Wpisz Roślinę" autoComplete="off" />
										</Cathegory>
										<Cathegory>
											<Title>Zwierzę</Title>
											<Input id="animal" className="input" onChange={this.updateData} placeholder="Wpisz Zwierzę" autoComplete="off" />
										</Cathegory>
										<Cathegory>
											<Title>Rzecz</Title>
											<Input id="item" className="input" onChange={this.updateData} placeholder="Wpisz Rzecz" autoComplete="off" />
										</Cathegory>
										<Cathegory>
											<Title>Imię</Title>
											<Input id="name" className="input" onChange={this.updateData} placeholder="Wpisz Imię" autoComplete="off" />
										</Cathegory>
									</Cathegories>
									{!this.state.timer && <Check onClick={this.ready} />}
									<Users>
										{this.state.userImages.map((url, index) => <Avatar src={url} alt={`user${index + 1}`} />)}
										<Letter>{this.state.letter}</Letter>
										{/* {this.state.userImages.map((url, index) => 'hmm')} */}
									</Users>
								</>}
								<Timer>{this.state.seconds !== '-' ? `${this.state.seconds}s` : '-'}</Timer>
							</All>}
					</>}
				</GameSite> : <Info onClick={this.logIn} />}
			</>
		);
	}
}

export default Game