// PACKAGES
import React, { Component } from 'react'
import styled, {keyframes} from 'styled-components'
import {db, au} from '../../../../Config/firebase'
import { paramCase } from "param-case";
import latinize from 'latinize'
// ICONS
import {FaDoorOpen, FaTimes, FaPlus} from 'react-icons/fa'
// COMPONENTS
import Info from '../../components/Info'

const CountryAndCitiesComponent = styled.div`
	width: 100vw;
	height: 100vh;
	height: calc(var(--vh, 1vh) * 100);
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 30px;
`
const Teams = styled.div`
	height: calc(100vh - 90px);
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	overflow-y: scroll;
	::-webkit-scrollbar {
		width: 0;
	}
`
const fadeTeam = keyframes`
	from {
		opacity: 0;
		transform: scale(0.9);
	}
	to {
		opacity: 1;
		transform: scale(1);
	}
`
const Team = styled.div`
	position: relative;
	width: 200px;
	height: 150px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-around;
	margin: 20px;
	padding: 20px;
	border: 2px solid var(--color-main);
	border-radius: 20px;
	animation: ${fadeTeam} 0.3s both;
`
const Delete = styled(FaTimes)`
	position: absolute;
	top: 10px;
	right: 10px;
	font-size: 20px;
	:hover {
		cursor: pointer;
	}
`
const TeamName = styled.h3`
	/* color: var(--color-decorative); */
	margin: 10px 0;
	width: 100%;
	text-align: center;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
`
const Join = styled(FaDoorOpen)`
	color: var(--color-decorative);
	font-size: 30px;
	:hover {
		cursor: pointer;
	}
`
const Create = styled(FaPlus)`
	color: var(--color-primary);
	font-size: 30px;
	margin-top: 20px;
	:hover {
		cursor: pointer;
	}
`

class CountryAndCities extends Component {
	state = {
		userId: null,
		isAdmin: false,
		groups: []
	}
	componentDidMount() {
		au.onAuthStateChanged(user => {
			if (user) {
				this.setState({userId: user.uid})
				user.getIdTokenResult().then(idTokenResult => {
					if (idTokenResult.claims.admin) {
						this.setState({isAdmin: true})
					}
					user.admin = idTokenResult.claims.admin
				})
			}
		})
		db.collection('apps-data').onSnapshot(snaps => {
			const groups = [];
			snaps.forEach(snap => groups.push({name: snap.id, owner: snap.data().owner}))
			this.setState({groups})
		})
	}
	create = () => {
		db.collection('apps-data').get().then(snaps => {
			const name = prompt("Wpisz nazwę grupy:")
			if (name) {
				if (!this.state.groups.includes(name)) {
					db.collection('apps-data').doc(name).set({
						letter: '',
						owner: this.state.userId,
						round: 0,
						seconds: '-',
						timer: false,
						choosing: true,
						users: {
							[this.state.userId]: {
								points: 0,
								rounds: [],
								addData: false
							}
						}
					})
					db.collection('users').doc(this.state.userId).update({
						'groups.country-and-cities': [
							{
								name: name,
								owner: true
							}
						]
					})
				}
				else {
					alert("Ta nazwa jest juz zajęta!")
				}
			}
		})
	}
	delete = (group, e) => {
		const name = e.currentTarget.parentElement.children[1].innerHTML;
		if (group.owner === this.state.userId || this.state.isAdmin) {
			db.collection('apps-data').doc(name).delete()
		}
	}
	join = (group) => {
		// this.props.history.push(`/panstwa-miasta/${group.owner}/${paramCase(latinize(group.name))}`)
		this.props.history.push(`/panstwa-miasta/${group.name}`)
	}
	logIn = () => {
		this.props.history.push('/logowanie')
	}
	render() {
		return (
			<>
				{!this.state.userId ? <Info onClick={this.logIn} /> : <CountryAndCitiesComponent>
					<Teams>
						{this.state.groups.map((group, index) => <Team key={index}>
							{(this.state.isAdmin || group.owner === this.state.userId) && <Delete onClick={(e) => this.delete(group, e)} />}
							<TeamName>{group.name}</TeamName>
							<Join onClick={() => this.join(group)} />
						</Team>)}
					</Teams>
					<Create onClick={this.create} />
				</CountryAndCitiesComponent>}
			</>
		);
	}
}

export default CountryAndCities